import React, {useState, useEffect} from "react";
import PropTypes from "prop-types";
import keycode from "keycode";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableFooter from "@material-ui/core/TableFooter";
import TableHead from "@material-ui/core/TableHead";
import TablePagination from "@material-ui/core/TablePagination";
import TableRow from "@material-ui/core/TableRow";
import TableSortLabel from "@material-ui/core/TableSortLabel";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import IconButton from "@material-ui/core/IconButton";
import Tooltip from "@material-ui/core/Tooltip";
import PlaylistPlayIcon from "@material-ui/icons/PlaylistAdd";
import CardEditMenu from './CardEditMenu.js'
import SearchBox from '../../../../../components/SearchBox/index';
import {useSelector} from 'react-redux';
import {useHistory} from 'react-router-dom';
import CircularProgress from '@material-ui/core/CircularProgress';
import {base_url,public_url, $http} from "config";
import Avatar from '@material-ui/core/Avatar';
import {NotificationContainer, NotificationManager} from 'react-notifications';
import IntlMessages from 'util/IntlMessages';
import SweetAlert from 'react-bootstrap-sweetalert';
import { jsPDF } from "jspdf";
//import { Page, Text, View, Document, StyleSheet } from '@react-pdf/renderer';


const columnData = [
  {id: "customer", align: false, disablePadding: true, label: "Customer"},
  {id: "project_title", align: true, disablePadding: false, label: "Project Title"},
  {id: "account_manager", align: true, disablePadding: false, label: "Account Manager"},
  {id: "room_count", align: true, disablePadding: false, label: "Rooms"},
  {id: "site_count", align: true, disablePadding: false, label: "Sites"},
  {id: "survey_start_date", align: true, disablePadding: false, label: "Survey Date"},
  {id: "signed_off", align: true, disablePadding: false, label: "Signed Off"},
  {id: "created_by", align: true, disablePadding: false, label: "User"},
  {id: "actions", align: true, disablePadding: false, numeric:'right', label: "Actions"}
];

const SiteSurveyTableHead = (props) => {

  const createSortHandler = property => event => {
    props.onRequestSort(event, property);
  };

    const {order, orderBy} = props;

    return (
      <TableHead>
        <TableRow>
          {columnData.map(column => {
            return (
              <TableCell
                key={column.id}
                align={column.numeric}
              >
                <Tooltip
                  title="Sort"
                  placement={column.numeric ? "bottom-end" : "bottom-start"}
                  enterDelay={300}
                >
                    <TableSortLabel
                    active={orderBy === column.id}
                    direction={order}
                    onClick={createSortHandler(column.id)}
                  >
                    {column.label}
                  </TableSortLabel>
                </Tooltip>
              </TableCell>
            );
          })}
        </TableRow>
      </TableHead>
    );
};

SiteSurveyTableHead.propTypes = {
  onRequestSort: PropTypes.func.isRequired,
  order: PropTypes.string.isRequired,
  orderBy: PropTypes.string.isRequired
};

const SiteSurveyTable = (props) => {
  const {filterKind, SiteSurveys, loader, goback, init_Data} = props;
  const nowuser = JSON.parse(localStorage.getItem("user"));
  const [order, setOrder] = useState("asc");
  const [orderBy, setOrderBy] = useState("id");
  const {width} = useSelector(({settings}) => settings);
  const History = useHistory();

  const [page, setPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(10);

  const handleRequestSort = (event, property) => {

    let sort_order;
    if (orderBy === property && order === "asc") {
      sort_order = "desc";
    } else {
      sort_order = "asc";
    }

    
    sort_order === "desc"
      ? SiteSurveys.sort((a, b) => (b[orderBy] < a[orderBy] ? -1 : 1))
      : SiteSurveys.sort((a, b) => (a[orderBy] < b[orderBy] ? -1 : 1));

    
    setOrder(sort_order);
    setOrderBy(property);
  };

  const onSetFavourite = (data) => {
    var fav_val = 0;
    fav_val = data.favourite ==='1'? 0 :1;
    const frmData = new FormData();
    frmData.append('id', data.id);
    frmData.append('favourite', fav_val);
    $http.post(base_url + "project/setFavourite", frmData)
    .then((response) => {
      if (response.data.status === "success") {
          NotificationManager.info("You set favourite!");
          init_Data();
      } else if (response.data.status === "error") {
          NotificationManager.error(response.data.msg, "Error!", 1000, () => {
          });
      }
     
    }).catch((error)=>{
      NotificationManager.error(error, "Error!", 1000, () => {
      });
  })
  }

  const handleClick = (item) => {
    History.push({
      pathname: `/app/project/live/${item.id}`
    })
  };

  const handleChangePage = (event, page) => {
    setPage(page);
  };

  const handleChangeRowsPerPage = event => {
    setRowsPerPage(event.target.value);
  };
  const printProject=(data)=>{
    //let sel_project_name = SiteSurveys.find(item=>item.id ==data).project_name
    $http.get(base_url + "project/projectInfo", {params : {project_id: data}})
    .then((response) => {
        var doc = new jsPDF('p', 'pt', 'letter');
        var is_upload = nowuser.is_upload >= 0?nowuser.is_upload:1;
        var front_cover = nowuser.front_cover
        var back_cover = nowuser.back_cover
        var Project = response.data.project
        var Rooms = response.data.rooms
        var Products = response.data.products
        var sites = response.data.sites
        const product_action_rows = ([
          {id: 0, content: 'New Product', icon: 'zmdi zmdi-assignment-check zmdi-hc-fw'},
          {id: 1, content: 'Dispose', icon: 'zmdi zmdi-close-circle-o zmdi-hc-fw'},
          {id: 2, content: 'Move To Room', icon: 'zmdi zmdi-arrows zmdi-hc-fw'}
        ]);
        var doc = new jsPDF('p', 'pt', 'letter');
        var width = doc.internal.pageSize.getWidth();
        var height = doc.internal.pageSize.getHeight();
        var img = new Image();
        var img_ext = '';
        let shift = 0;
        var x0 = 80,y0 = 80;
        if(is_upload && front_cover){
            //img.src = '/pdf_front.png';
            img.src = public_url + 'upload/img/' + front_cover
            doc.addImage(img, 'png', 0, 0, width, height);
        y0 = 250;
            doc.setFontSize(30);
            doc.text('Scope of works:', 250, y0);
            img.src = public_url + 'upload/img/' + Project.logo_img 
            img_ext = Project.logo_img .split('.').pop()
            doc.addImage(img, img_ext, 250, y0+30, 200, 130);
            y0 += 200
            doc.setFontSize(12);
            doc.text("Client: ", 250, y0);
            doc.text(Project.company_name+'', 350, y0);
            doc.text("Project Title: ", 250, y0+30);
            doc.text(Project.project_name+'', 350, y0+30);
            doc.text("Sirvey Date: ", 250, y0+60);
            doc.text(Project.survey_start_date+'', 350, y0+60);
            
        doc.addPage();
        }
        Rooms.map(sel_room=>{
            var sel_site = sites.find(item=>(item.id == sel_room.site_id))
            y0 = 100
            doc.setFontSize(30);
            doc.text("Room  "+sel_room.room_number+'  Summary', 70, y0);
            y0 += 50
            doc.setFontSize(12);
            doc.text("Customer User: ", 50, y0);
            doc.setFontSize(11);
            doc.text(Project.customer_user+'', 60+width/4, y0);
            doc.setFontSize(12);
            doc.text("Address1: ", width/2, y0);
            doc.setFontSize(11);
            doc.text(sel_site.address+'', 2*width/3, y0);
            y0 += 20
            doc.setFontSize(12);
            doc.text("Survey Date: ", 50, y0);
            doc.setFontSize(11);
            doc.text(Project.survey_start_date+'', 60+width/4, y0);
            doc.setFontSize(12);
            doc.text("Address2: ", width/2, y0);
            doc.setFontSize(11);
            doc.text(sel_site.address1+'', 2*width/3, y0);
            y0 += 20
            doc.setFontSize(12);
            doc.text("Number of rooms: ", 50, y0);
            doc.setFontSize(11);
            doc.text(Project.room_count+'', 60+width/4, y0);
            doc.setFontSize(12);
            doc.text("City: ", width/2, y0);
            doc.setFontSize(11);
            doc.text(sel_site.city+'', 2*width/3, y0);
            y0 += 20
            doc.setFontSize(12);
            doc.text("Out of hours Contat Number: ", 50, y0);
            doc.setFontSize(11);
            doc.text(Project.contact_number+'', 60+width/4, y0);
            doc.setFontSize(12);
            doc.text("Postcode: ", width/2, y0);
            doc.setFontSize(11);
            doc.text(sel_site.postcode+'', 2*width/3, y0);

            y0 +=30
            doc.setFontSize(13);
            doc.text('-   Products to be installed', 50, y0);
            y0 = y0+30
            doc.setFontSize(11);
            doc.text('No', 70, y0);
            doc.text('Name', width/5, y0);
            doc.text('Action', 2*width/5, y0);
            doc.text('Qty', 3*width/5, y0);
            doc.text('Description', 7*width/10, y0);
            
            var sel_products = Products.filter(item=>item.id==sel_room.id)
            sel_products.filter(item=>item.action ==0).map((product,index)=>{
                y0 +=20
                if(y0> height-100)
                {
                    doc.addPage();
                    y0 = 80;
                }
                doc.text(index+1+'', 70, y0);
                doc.text(product.product_name+'', width/5, y0);
                doc.text(product_action_rows[product.action].content, 2*width/5, y0);
                doc.text(product.qty+'', 3*width/5, y0);
                doc.text(product.description+'', 4*width/5, y0);
            })
            y0 +=30
            doc.setFontSize(13);
            doc.text('-   Products to be Dispose of', 50, y0);
            y0 = y0+30
            doc.setFontSize(11);
            doc.text('No', 70, y0);
            doc.text('Name', width/5, y0);
            doc.text('Action', 2*width/5, y0);
            doc.text('Qty', 3*width/5, y0);
            doc.text('Description', 7*width/10, y0);
            sel_products.filter(item=>item.action ==1).map((product,index)=>{
                y0+=20
                if(y0> height-100)
                {
                    doc.addPage();
                    y0 = 80;
                    shift = index;
                }
                doc.text(index+1+'', 70, y0);
                doc.text(product.product_name+'', width/5, y0);
                doc.text(product_action_rows[product.action].content, 2*width/5, y0);
                doc.text(product.qty+'', 3*width/5, y0);
                doc.text(product.description+'', 4*width/5, y0);
            })
            y0 +=30
            doc.setFontSize(13);
            doc.text('-   Products to be Moved', 50, y0);
            y0 = y0+30
            doc.setFontSize(11);
            doc.text('No', 70, y0);
            doc.text('Name', width/5, y0);
            doc.text('Action', 2*width/5, y0);
            doc.text('Qty', 3*width/5, y0);
            doc.text('Description', 7*width/10, y0);
            sel_products.filter(item=>item.action ==2).map((product,index)=>{
                y0+=20
                if(y0> height-100)
                {
                    doc.addPage();
                    y0 = 80;
                    shift = index;
                }
                doc.text(index+1+'', 70, y0);
                doc.text(product.product_name+'', width/5, y0);
                doc.text(product_action_rows[product.action].content, 2*width/5, y0);
                doc.text(product.qty+'', 3*width/5, y0);
                doc.text(product.description+'', 4*width/5, y0);
            })
            doc.addPage();
            doc.setFontSize(15);
            doc.text(sel_room.room_number+'(Room Name)', 70, 50);
            y0 = 80
            x0 = 80
            shift = 0;
            sel_room.img_files.map((item,index)=>{
                if(x0+200*(index-shift) > height-200)
                {
                    doc.addPage();
                    x0 = 80;
                    shift = index;
                }
                // img.src = '/temp.jpg'
                // doc.addImage(img, 'jpg', 100, x0+200*(index-shift), width-200, 200);
                img.src = public_url + 'upload/img/' + item.img_name
                img_ext = item.img_name.split('.').pop()
                doc.addImage(img, img_ext, 100, x0+200*(index-shift), width-200, 200);
                
                x0 = x0+20
                y0 = x0+200*(index-shift+1)+30;
            })
            if(y0 > height-100)
            {
                doc.addPage();
                y0 = 80;
            }
            doc.setFontSize(12);
            doc.text("-  Note: ", 50, y0);
            doc.setFontSize(11);
            let note_lines = sel_room.notes.split(/\r\n|\r|\n/)
            y0 = y0+30
            note_lines.map((item,index)=>{
                if (item =='null') item = ''
                doc.text(item+'', 70, y0+20*index)
            })

            y0 = y0+20*note_lines.length
          doc.addPage();
        })
        if(is_upload && back_cover){
          //img.src = '/pdf_back.png';
          img.src = public_url + 'upload/img/' + back_cover
            doc.addImage(img, 'png', 0, 0, width, height);
        }
        doc.save("Scope of Works(project).pdf");
    })
  };
  
  const onSelectSiteSurvey = (data, index) => {
    if (index === 0) {
      
      //let path_name = SiteSurveys.find((item) => {return item.id == data})
      History.push({
        pathname: `/app/project/live/${data}`
      })
    } else if (index === 1) {
      History.push({
        pathname: "/app/project/add-new",
        state: {id: data}
      })
    }
    else if (index === 3) {
        printProject(data)
    } else if (index === 2) {
      const frmData = new FormData();
      frmData.append('id', data);
      $http.post(base_url + "project/deleteProject",frmData)
      .then((response) => {
        if (response.data.status === "success") {
            init_Data();
            NotificationManager.info("You removed selected sticker");
        } else if (response.data.status === "error") {
            NotificationManager.error(response.data.msg, "Error!", 1000, () => {
            });
        }
        
      }).catch((error)=>{
        NotificationManager.error(error, "Error!", 1000, () => {
      });

      if (!navigator.onLine) {
        caches.open('SirvezApp').then(function(cache) {
          cache.match('/api/project/projectList?archived=0')
          .then(function(response) {return response ? response.json() : null})
          .then(function(responsedata){
              if(responsedata==null) return
              responsedata.projects = responsedata.projects.filter((item) => {return item.id != data})
              const jsonResponse = new Response(JSON.stringify(responsedata), {
                headers: {'content-type': 'application/json'}
              })
              cache.put('/api/project/projectList?archived=0', jsonResponse.clone())
              .then(function(){
               init_Data()
              })
          })
        })
      }
    })
    }
  }

  return (
    <div>
      {
        loader ? (
          <div className="flex-auto">
            <div className="table-responsive-material">
              <Table className="">
                <SiteSurveyTableHead
                  order={order}
                  orderBy={orderBy}
                  onRequestSort={handleRequestSort}
                />
                <TableBody>
                  {
                    SiteSurveys.length > 0 ?
                  SiteSurveys.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage).map((n) => 
                      <TableRow
                        hover
                        key={n.id}
                        // onClick={event => handleClick(event, n.id)}
                        //onKeyDown={event => handleKeyDown(event, n.id)}
                        tabIndex={-1}
                      >
                        <TableCell onClick={() => handleClick(n)}>
                          <IconButton className="icon-btn" onClick={() => {
                            onSetFavourite(n);
                          }}>
                            {n.favourite ==='1' ?
                              <i className="zmdi zmdi-star text-warning"/> :
                              <i className="zmdi zmdi-star-outline text-warning"/>
                            }

                          </IconButton>
                          {n.customer}</TableCell>
                        <TableCell onClick={() => handleClick(n)}>{n.project_name}</TableCell>
                        <TableCell onClick={() => handleClick(n)}>{n.account_manager}</TableCell>
                        <TableCell onClick={() => handleClick(n)}>{n.room_count}</TableCell>
                        <TableCell onClick={() => handleClick(n)}>{n.site_count}</TableCell>
                        <TableCell onClick={() => handleClick(n)}>{n.survey_start_date}</TableCell>
                        <TableCell onClick={() => handleClick(n)}>{n.signed_off ? 'Yes':'No'}</TableCell>
                        <TableCell onClick={() => handleClick(n)}>
                          <div className="user-profile d-flex flex-row align-items-center">
                              <Avatar
                                  alt={n.account_manager}
                                  src={public_url + "upload/img/" + n.profile_pic}
                                  className="user-avatar"
                              />
                          </div>
                        </TableCell>
                        <TableCell className="text-right">
                          <CardEditMenu key={n.id} selectedData={n.id} 
                            onSelectRow={onSelectSiteSurvey}/>
                        </TableCell>
                      </TableRow>
                    )
                    :
                    <tr>
                        <td className="text-danger" colSpan="10">
                            <IntlMessages id="table.noData"/>
                        </td>
                    </tr>
                  }
                </TableBody>
                <TableFooter>
                  <TableRow>
                    <TablePagination
                      count={SiteSurveys.length}
                      rowsPerPage={rowsPerPage}
                      page={page}
                      onChangePage={handleChangePage}
                      onChangeRowsPerPage={handleChangeRowsPerPage}
                    />
                  </TableRow>
                </TableFooter>
              </Table>
            </div>
            <NotificationContainer/>
          </div>
        ):(
          <div className="loader-view"
                  style={{height: width >= 1200 ? 'calc(100vh - 259px)' : 'calc(100vh - 238px)'}}>
              <CircularProgress/>
          </div>        
        )
      }
        <SweetAlert show={goback}
            warning
            confirmBtnText="Go Back"
            confirmBtnBsStyle="danger"
            cancelBtnBsStyle="default"
            title="Warning!"
            onConfirm={() => History.go(-1)}
        >                
        Data not cached, so you can not view this page.
        </SweetAlert>

    </div>
  );
};

export default SiteSurveyTable;
