import React from 'react';
import PropTypes from 'prop-types';
import {withStyles} from '@material-ui/core/styles';
import classNames from 'classnames';
import Drawer from '@material-ui/core/Drawer';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import List from '@material-ui/core/List';
import Typography from '@material-ui/core/Typography';
import Divider from '@material-ui/core/Divider';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import InboxIcon from '@material-ui/icons/Inbox';
import StarIcon from '@material-ui/icons/Star';
import CheckIcon from '@material-ui/icons/Check';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import SiteSurveyTable from './SiteSurveyTable';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import TextField from '@material-ui/core/TextField';
import MenuItem from '@material-ui/core/MenuItem';
import IntlMessages from 'util/IntlMessages';
import {base_url, public_url, $http} from "config";

const drawerWidth = 240;

const styles = theme => ({
  root: {
    width: '100%',
    height: '100%',
    zIndex: 1,
    overflow: 'hidden',
  },
  appFrame: {
    position: 'relative',
    display: 'flex',
    width: '100%',
    height: '100%',
  },
  appBar: {
    position: 'absolute',
    transition: theme.transitions.create(['margin', 'width'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
  },
  appBarShift: {
    marginLeft: drawerWidth,
    width: `calc(100% - ${drawerWidth}px)`,
    transition: theme.transitions.create(['margin', 'width'], {
      easing: theme.transitions.easing.easeOut,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  menuButton: {
    marginLeft: 12,
    marginRight: 20,
  },
  hide: {
    display: 'none',
  },
  drawerPaper: {
    position: 'relative',
    height: '100%',
    width: drawerWidth,
  },
  drawerHeader: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'flex-end',
    padding: '0 8px',
    ...theme.mixins.toolbar,
  },
  content: {
    width: '100%',
    marginLeft: -drawerWidth,
    flexGrow: 1,
    padding: theme.spacing(3),
    transition: theme.transitions.create('margin', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    marginTop: 56,
    [theme.breakpoints.up('sm')]: {
      content: {
        height: 'calc(100% - 64px)',
        marginTop: 64,
      },
    },
  },
  contentShift: {
    marginLeft: 0,
    transition: theme.transitions.create('margin', {
      easing: theme.transitions.easing.easeOut,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
});

class PersistentProject extends React.Component {
  state = {
    open: false,
    filter_kind: 0,
    SiteSurveys: [],
    loader : false,
    goback : false,
    alldata: [],
    users: [],
    user_id: 0,
    customers: [],
    customer_id: 0
  };

  handleDrawerOpen = () => {
    this.setState({open: true});
  };

  handleDrawerClose = () => {
    this.setState({open: false});
  };

  init_Data = () => {
    $http.get(base_url + "project/projectList", {params: {archived: 0}})
    .then((response) => {
      this.setState({
        SiteSurveys: response.data.projects,
        alldata: response.data.projects,
        loader: true,
        users: response.data.users,
        customers: response.data.customers
      })
    })
    .catch((error) => {
      this.setState({goback: true})
    })
  }

  setCustomer = (value) => {
  }

  setUser = (value) => {
    this.setState({user_id: value})
  }

  filter = (kind, customerid, userid) => {
    this.setState({
      filter_kind: kind,
      customer_id: customerid,
      user_id: userid,
      SiteSurveys: this.state.alldata.filter((item) => {
        return ((kind == 0) 
            || (kind == 1 && item.favourite == '1') 
            || (kind == 2 && item.signed_off == '2'))
          && ((customerid == 0)
            || (customerid > 0 && item.company_id == customerid))
          && ((userid == 0)
            || (userid > 0 && item.created_by == userid))
      })
    })
    
  }

  componentDidMount = () => {
    this.init_Data()
  }

  render() {
    const {classes, theme} = this.props;
    const nowuser = JSON.parse(localStorage.getItem("user"));

    return (
      <div className={classes.root}>
        <div className={classes.appFrame}>
          <AppBar className={classNames('bg-primary', classes.appBar, this.state.open && classes.appBarShift)}>
            <Toolbar disableGutters={!this.state.open}>
              <IconButton
                aria-label="open drawer"
                onClick={this.handleDrawerOpen}
                className={classNames(classes.menuButton, this.state.open && classes.hide)}>

                <MenuIcon className="text-white"/>
              </IconButton>
              <Typography variant="h6" color="inherit" className="text-white" noWrap>
                Projects
              </Typography>
            </Toolbar>
          </AppBar>
          <Drawer
            variant="persistent"
            classes={{
              paper: classes.drawerPaper,
            }}
            open={this.state.open}
          >
            <div className={classes.drawerInner}>
              <div className={classes.drawerHeader}>
                <IconButton onClick={this.handleDrawerClose}>
                  {theme.direction === 'rtl' ? <ChevronRightIcon/> : <ChevronLeftIcon/>}
                </IconButton>
              </div>
              <Divider/>
              {nowuser.user_type<6?
              <List className={classes.list}>
                <ListItem>
                  <TextField
                    id="customer"
                    select
                    label="Customer"
                    value={this.state.customer_id}
                    onChange={(event)=>this.filter(this.state.filter_kind, event.target.value, this.state.user_id)}
                    margin="normal"
                    fullWidth
                  >
                    <MenuItem value={0}>
                        <IntlMessages id="Task.all"/>
                    </MenuItem>
                    {this.state.customers.map(option => (
                        <MenuItem key={option.id} value={option.id}>
                            {option.name}
                        </MenuItem>
                    ))}
                  </TextField>
                </ListItem>
              </List>
              :null}
              {nowuser.user_type<6?
              <List className={classes.list}>
                <ListItem>
                  <TextField
                    id="user"
                    select
                    label="User"
                    value={this.state.user_id}
                    onChange={(event)=>this.filter(this.state.filter_kind, this.state.customer_id, event.target.value)}
                    margin="normal"
                    fullWidth
                  >
                    <MenuItem value={0}>
                        <IntlMessages id="Task.all"/>
                    </MenuItem>
                    {this.state.users.map(option => (
                        <MenuItem key={option.id} value={option.id}>
                            {option.first_name}
                        </MenuItem>
                    ))}
                  </TextField>
                </ListItem>
              </List>
              :null}
              <Divider/>
              <List className={classes.list}>
                <ListItem button 
                  className="py-0"
                  onClick={() => this.filter(0, this.state.customer_id, this.state.user_id)}>
                  <ListItemIcon>
                    <IconButton className={`${this.state.filter_kind == 0 ? 'text-primary' : ''}`}>
                      <InboxIcon/>
                    </IconButton>
                  </ListItemIcon>
                  <ListItemText primary="All" className={`${this.state.filter_kind == 0 ? 'text-primary' : ''}`}/>
                </ListItem>
                <ListItem button 
                  className="py-0"
                  onClick={() => this.filter(1, this.state.customer_id, this.state.user_id)}>
                  <ListItemIcon>
                    <IconButton className={`${this.state.filter_kind == 1 ? 'text-primary' : ''}`}>
                      <StarIcon/>
                    </IconButton>
                  </ListItemIcon>
                  <ListItemText primary="Star" className={`${this.state.filter_kind == 1 ? 'text-primary' : ''}`}/>
                </ListItem>
                <ListItem button 
                  className="py-0"
                  onClick={() => this.filter(2, this.state.customer_id, this.state.user_id)}>
                  <ListItemIcon>
                    <IconButton className={`${this.state.filter_kind == 2 ? 'text-primary' : ''}`}>
                      <CheckIcon/>
                    </IconButton>
                  </ListItemIcon>
                  <ListItemText primary="Signed Off" className={`${this.state.filter_kind == 2 ? 'text-primary' : ''}`}/>
                </ListItem>
              </List>
            </div>
          </Drawer>
          <main className={classNames(classes.content, this.state.open && classes.contentShift)}>
            <SiteSurveyTable 
              loader={this.state.loader}
              goback={this.state.goback}
              SiteSurveys={this.state.SiteSurveys}
              init_Data={this.init_Data}
              />
          </main>
        </div>
      </div>
    );
  }
}

PersistentProject.propTypes = {
  classes: PropTypes.object.isRequired,
  theme: PropTypes.object.isRequired,
};

export default withStyles(styles, {withTheme: true})(PersistentProject);